import * as firebase from 'firebase/app';
import 'firebase/firestore';
import 'firebase/analytics';
import 'firebase/messaging';

export interface NotifyConfig {
  title: string;
  message: string;
  link?: string;
}

/* npm install firebase@7.2.3 */
class FirebaseRegister {
  private database!: firebase.firestore.Firestore;
  private analytics!: firebase.analytics.Analytics;
  private message!: firebase.messaging.Messaging;
  private config = {
    apiKey: 'AIzaSyDE10tLBnUsJiSErS_qhInhOlBvVDsZrAA',
    authDomain: 'debt-assistant.firebaseapp.com',
    databaseURL: 'https://debt-assistant.firebaseio.com',
    projectId: 'debt-assistant',
    storageBucket: 'debt-assistant.appspot.com',
    messagingSenderId: '232567755974',
    appId: '1:232567755974:web:22edb092ec8bb4074b73c7',
    measurementId: 'G-YNGS54G08J'
  };
  private mToken!: string;

  get db(): firebase.firestore.Firestore {
    return this.database;
  }

  get analysis(): firebase.analytics.Analytics {
    return this.analytics;
  }

  get messaging(): firebase.messaging.Messaging {
    return this.message;
  }

  get messagingToken(): string {
    return this.mToken;
  }

  init() {
    const app = firebase.initializeApp(this.config);
    this.analytics = app.analytics();
    this.database = app.firestore();
    this.message = app.messaging();
  }

  registerNotification() {
    this.message.usePublicVapidKey('BLUdzB2_QYh_6gbmWeyMkWt4Lt43Mp78Yl20tj6nv0kFIeAcqcNatpFemu_8kduwtypTaO8WW42GS2NiMjK7oWE');

    // if token refresh will trigger
    this.message.onTokenRefresh(() => {
      this.message
        .getToken()
        .then(refreshedToken => {
          console.log('Token refreshed.');
          this.mToken = refreshedToken;
          // Indicate that the new Instance ID token has not yet been sent to the
          // app server.
          // (window as any).setTokenSentToServer(false);
          // Send Instance ID token to app server.
          // (window as any).sendTokenToServer(refreshedToken);
        })
        .catch(err => {
          console.log('Unable to retrieve refreshed token ', err);
          // (window as any).showToken('Unable to retrieve refreshed token ', err);
        });
    });

    this.message
      .getToken()
      .then(currentToken => {
        if (currentToken) {
          this.mToken = currentToken;
          // (window as any).sendTokenToServer(currentToken);
          // (window as any).updateUIForPushEnabled(currentToken);
        } else {
          console.log('No Instance ID token available. Request permission to generate one.');
          return this.message.requestPermission().then(() => this.message.getToken());
          // Show permission UI.
          // (window as any).updateUIForPushPermissionRequired();
          // (window as any).setTokenSentToServer(false);
        }
      })
      .catch(err => {
        console.log('An error occurred while retrieving token. ', err);
        // (window as any).showToken('Error retrieving Instance ID token. ', err);
        // (window as any).setTokenSentToServer(false);
      });
  }

  notify(req: NotifyConfig) {
    const link = req.link
      ? {
          webpush: {
            fcm_options: {
              link: req.link
            }
          }
        }
      : {};

    fetch('https://fcm.googleapis.com/v1/projects/debt-assistant/messages:send', {
      method: 'POST',
      headers: {
        Authorization: `bearer ${this.messagingToken}`,
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        message: {
          notification: {
            title: req.title,
            body: req.message
          },
          ...link
        }
      })
    })
      .then(function(response) {
        console.log(response);
      })
      .catch(function(error) {
        console.error(error);
      });
  }

  private fetchMessagingToken() {}
}
const Firebase = new FirebaseRegister();
export default Firebase;
